import SplitContainer from "../../components/cms/SplitContainer/SplitContainer";
import React from "react";

enum ContentTypes {
    ContainerSplitContent = 1,
    Image,
    RichText,
}

interface IContentFulItem {
    sys: {
        id: string,
        contentType: {
            sys: {
                id: string
            }
        }
    }
}

function getComponent(item: IContentFulItem) {
    let typeId = item.sys.contentType.sys.id;
    typeId = typeId.charAt(0).toUpperCase() + typeId.substring(1);

    const id = item.sys.id;
    const type = ContentTypes[typeId];
    let Component;
    switch (type) {
        case ContentTypes.ContainerSplitContent:
            Component = SplitContainer;
            break;
        case ContentTypes.Image:
            Component = () => (<div key={id}>IMAGE ... from CMS Image</div>);
            break;
        case ContentTypes.RichText:
            Component = () => (<div key={id}>RICH TEXT ... from CMS RichText</div>);
            break;
        default:
            Component = () => (<div key={id}>TO BE IMPLEMENTED ... unknown from CMS</div>);
            break;
    }
    return <Component key={id} data={item} />
}

export default getComponent;