import React, {FunctionComponent} from 'react';
import getComponent from "../../../utils/cms/component-resolver";

import ClassNames from './FlexiblePage.module.scss';
import Navigation from "../../Navigation/Navigation";

interface Props {
    pageContext: {
        slug: string
        metaKeywords: string[];
        metaDescription: string;
        title: string;
        items: any[];
    };
}

const FlexiblePage: FunctionComponent<Props> = ({pageContext: {items, title}}) => {
    const components = items.map(item => {
        const Comp = getComponent(item);
        return Comp;
    });

    return (
        <div className={ClassNames.FlexiblePage}>
            <Navigation/>
            {components}
        </div>
    );
};

export default FlexiblePage;
